<template>
  <div class="widget-body">
    <loading v-if="!loaded"/>
    <div class="row" v-else>
      <vuestic-widget class="col" :headerText="render_title()">
        <div class="row show-buttons-area">
          <router-link
            :to="{ name: 'questions' }"
            class="btn btn-info btn-micro"
          >
            {{'menu.questions.questions' | translate}}
          </router-link>
          <router-link 
            v-if="id"              
            class="btn btn-primary btn-micro"
            :to="{ name: 'update-question', params: { id: this.id } }"
          >
            {{'buttons.update' | translate}}
          </router-link>
        </div>

        <div class="row">
        <div class="col-md-6 vuestic-timeline-item__after"><div class="vuestic-card card vuestic-card--theme-dark">
          <div class="card-body">
          <h5 class="vuestic-card__title card-title">{{'question.categories' | translate}}</h5>
          <p class="card-text" v-html="questions.question_type">
              
          </p></div></div></div></div>

        <div class="row vue-misc well" v-html="questions.question"></div>

        <div class="row vue-misc well" v-html="questions.answer"></div>

      </vuestic-widget>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import api from 'src/packages/Api'
  import Loading from 'partials/Loading'

  Vue.component('loading', Loading)

  export default {
    name: 'question',
    data () {
      return {
        questions: {},
        id: '',
        title: '',
        question: '',
        answer: '',
        showhomepage:0,       
        category_id: [],
        question_type:'',
        loaded: false
      }
    },
    created () {
      api.get(`questions/${this.$route.params.id}`).then(response => {
        const res = response.data

        this.questions = res
        this.id = res.id
        this.title = res.title


        this.loaded = true
      }).catch(error => {
        this.$store.dispatch('addToastMessage',
          { text: Vue.i18n.translate(`errors.${error.data.status_code}`) }
        )
        this.$router.push({ name: 'questions' })
      })
    },
    methods: {
      render_title () {
        return `${this.questions.title}`
      }
    }
  }
</script>

